<template>
    <div style="background-color: #f4f4f4;">
        <list>
            <cell style="background-color: white; padding: 16px; border-bottom-width: 2px; border-color: #f4f4f4"
                  v-for="item in resultList" @click="selectItem(item)">
                <text style="font-size: 16wx; color: #999999">{{item.disname}}</text>
            </cell>
        </list>
    </div>

</template>
<script>
    const stream = weex.requireModule('stream');

    export default {
        data() {
            return {
                msg : '',
                resultList : []
            }
        },

        created() {
            var me = this;
            stream.fetch({
                method: 'POST',
                url: "app/disease/queryPage",
                type: 'json',
                body: JSON.stringify({currentPageNo: 1, pageSize: 1000})
            }, function (ret) {
                if(ret.ok){
                    //me.msg = ret.data.page;
                    ret.data.page.result.forEach(function(item){
                        me.resultList.push({disid : item.disid, disname : item.disname})
                    });
                }
            });
        }
    }
</script>



